<template>
  <v-layout row wrap v-resize="onResize">
    <v-flex xs12>
      <v-card hover class="ma-2 pa-2">
        <v-card-title class="title">
          Current Water Flow (GPM)
        </v-card-title>
        <div ref='waterSankey' style='height:400px;width:100%'></div>
      </v-card>
    </v-flex>
  </v-layout>
</template>
<script>
export default{
  data: () => ({
    waterSankey: null,
    waterCollectionRate: 1, //gpm
    waterConsumptionRate: 5, //gpm
  }),
  methods: {
    refreshWaterSankey(){
      const waterSankeyOption = {
        tooltip: {
          trigger: 'item',
          triggerOn: 'mousemove'
        },
        series: [
        {
            type: 'sankey',
            focusNodeAdjacency: 'allEdges',
            lineStyle: {
                normal: {
                    color: 'source',
                    curveness: 0.5
                }
            },
            data: [
              {name: 'Rain', value: this.waterCollectionRate},
              {name: 'Atlanta', value: 0},
              {name: 'Kendeda', value: this.waterConsumptionRate},
              {name: 'Interior', value: Math.round(this.waterConsumptionRate/1.5*100)/100},
              {name: 'Irrigation', value: Math.round(this.waterConsumptionRate/3*100)/100},
            ],
            links: [
              {source: 'Rain', target: 'Kendeda', value: this.waterCollectionRate},
              {source: 'Atlanta', target: 'Kendeda', value: 0},
              {source: 'Kendeda', target: 'Interior', value: Math.round(this.waterConsumptionRate/1.5*100)/100},
              {source: 'Kendeda', target: 'Irrigation', value: Math.round(this.waterConsumptionRate/3*100)/100},
            ],
        },
        ],
      };
      this.waterSankey.setOption(waterSankeyOption,true);
    },
    onResize() {
      if(this.waterSankey) _.debounce(this.waterSankey.resize, 100)()
    },
  },
  mounted: function () {
    this.waterSankey = echarts.init(this.$refs.waterSankey);
    this.refreshWaterSankey();
    this.onResize();
  }
}
</script>
