<template>
<div>
  <v-layout v-resize='onResize'>
    <v-flex xs12 class='pa-2'>
      <v-card class='mt-3 pt-1 mx-auto' max-width='400'>
        <v-sheet class='v-sheet--offset mx-auto pa-0' :color='color' elevation='12' max-width='calc(100% - 32px)' height='120'>
          <!-- <v-sparkline :labels='labels' :value='value()' color='white' line-width='2' padding='16' height='120' /> -->
          <div :ref='ref' class='pt-3' style='width: 100%; height: 110px;' :style='setStyle' />
        </v-sheet>

        <v-card-text class='pt-0'>
          <div class='title font-weight-light mb-2'><b>{{ title }}</b></div>
          <div class='subheading font-weight-light grey--text'>{{ desc }}</div>
          <v-divider class='my-2' />
          <v-icon class='mr-2' small>mdi-clock</v-icon>
          <span class='caption grey--text font-weight-light'>last update {{ random(20) }} minutes ago</span>
        </v-card-text>
      </v-card>
    </v-flex>
  </v-layout>
</div>
</template>


<script>
export default {
  props: ['title', 'desc', 'color'],
  data: () => ({
    chart: null,
    labels: ['12am', '3am', '6am', '9am', '12pm', '3pm', '6pm', '9pm'],
  }),
  computed: {
    ref() {
      return `echartsCard${ this.title }`
    },
    setStyle() {
      return {
        background: this.color
      }
    }
  },
  methods: {
    valueIns() {
      return _.times(_.size(this.labels), i => _.random(200, 600))
    },
    valueAcu() {
      const arr = []
      _.each(this.labels, i => arr.push((_.last(arr) || 0) + _.random(200, 600)))
      return arr
    },
    drawChart(option) {
      const opt = option || {
        tooltip: {
          trigger: 'axis'
        },
        grid: {
          // containLabel: true,
          top: 5,
          left: 10,
          right: 10,
          bottom: 20,
          // width: '100%',
          // height: '100%',
        },
        xAxis: [{
          type: 'category',
          axisLine: {
            show: false,
          },
          axisTick: {
            show: false,
            alignWithLabel: true
          },
          // show: false,
          data: _.map(this.labels, value => ({
            value,
            textStyle: {
              color: 'white',
              fontSize: 8
            }
          })),
        }],
        yAxis: [{
          show: false,
          type: 'value',
          name: 'line',
          min: 0,
          max: 600,
          // axisLabel: {
          //   formatter: '{value} %'
          // }
        }, {
          show: false,
          type: 'value',
          name: 'line',
          min: 0,
          max: 3600,
          // axisLabel: {
          //   formatter: '{value} %'
          // }
        }],
        series: [{
          name: 'current',
          type: 'line',
          lineStyle: {
            normal: {
              width: 3,
              color: 'white',
              shadowColor: 'rgba(0,0,0,0.7)',
              shadowBlur: 10,
              shadowOffsetY: 10
            }
          },
          smooth: false,
          data: this.valueIns()
        }, {
          name: 'cumulative',
          type: 'bar',
          stack: '总量',
          yAxisIndex: 1,
          itemStyle: {
            color: 'rgba(255, 255, 255, 0.55)',
            barBorderRadius: [3, 3, 0, 0]
          },
          data: this.valueAcu()
        }]
      }

      this.chart.setOption(opt)
      this.onResize()
    },
    onResize() {
      if(this.chart) _.debounce(this.chart.resize, 100)()
    },
    random(x) {
      return _.random(5, x)
    }
  },
  mounted() {
    this.chart = echarts.init(this.$refs[this.ref], 'shine')
    _.delay(this.drawChart, 1)
  }
}
</script>

<style lang='stylus'>
.v-sheet--offset
  top: -24px
  position: relative
</style>
