<template>
  <v-layout row wrap v-resize="onResize">
    <v-flex xs12>
      <v-card hover class="ma-2 pa-2">
        <v-card-title class="title">
          Current Power Flow (kW)
        </v-card-title>
        <div ref='powerSankey' style='height:400px;width:100%'></div>
      </v-card>
    </v-flex>
  </v-layout>
</template>
<script>
export default{
  data: () => ({
    powerSankey: null,
    solarPower: 8,
    elecConsumptionRate: 90,
  }),
  methods: {
    refreshPowerSankey(){
      const powerSankeyOption = {
        tooltip: {
          trigger: 'item',
          triggerOn: 'mousemove'
        },
        series: [
        {
            type: 'sankey',
            focusNodeAdjacency: 'allEdges',
            lineStyle: {
                normal: {
                    color: 'source',
                    curveness: 0.5
                }
            },
            data: [
              {name: 'Solar Panels', value: this.solarPower*12},
              {name: 'Kendeda', value: this.elecConsumptionRate},
              {name: 'Mechanical & Plumbing', value: this.elecConsumptionRate/2},
              {name: 'Plug', value: this.elecConsumptionRate/8},
              {name: 'Lighting', value: this.elecConsumptionRate/8},
              {name: 'Elevator', value: this.elecConsumptionRate/8},
              {name: 'Battery Panel', value: this.elecConsumptionRate/8},
              {name: 'Heating', value: this.elecConsumptionRate/12},
              {name: 'Cooling', value: this.elecConsumptionRate/12},
              {name: 'Hot Water', value: this.elecConsumptionRate/12},
              {name: 'Fans', value: this.elecConsumptionRate/12},
              {name: 'Pumps', value: this.elecConsumptionRate/12},
              {name: 'Water Treatment', value: this.elecConsumptionRate/12},
              {name: 'Maker Space', value: this.elecConsumptionRate/32},
              {name: 'Coffee Cart', value: this.elecConsumptionRate/32},
              {name: 'Computer Services', value: this.elecConsumptionRate/32},
              {name: 'Miscellaneous', value: this.elecConsumptionRate/32},
              {name: 'Campus Grid', value: this.solarPower*12-this.elecConsumptionRate},
            ],
            links: [
              //{source: 'Campus Grid', target: 'Kendeda', value: this.elecConsumptionRate-this.solarPower*12},
              {source: 'Solar Panels', target: 'Kendeda', value: this.solarPower*12},
              {source: 'Solar Panels', target: 'Campus Grid', value: this.solarPower*12-this.elecConsumptionRate},
              {source: 'Kendeda', target: 'Mechanical & Plumbing', value: this.elecConsumptionRate/2},
              {source: 'Kendeda', target: 'Plug', value: this.elecConsumptionRate/8},
              {source: 'Kendeda', target: 'Lighting', value: this.elecConsumptionRate/8},
              {source: 'Kendeda', target: 'Elevator', value: this.elecConsumptionRate/8},
              {source: 'Kendeda', target: 'Battery Panel', value: this.elecConsumptionRate/8},
              {source: 'Mechanical & Plumbing', target: 'Heating', value: this.elecConsumptionRate/12},
              {source: 'Mechanical & Plumbing', target: 'Cooling', value: this.elecConsumptionRate/12},
              {source: 'Mechanical & Plumbing', target: 'Hot Water', value: this.elecConsumptionRate/12},
              {source: 'Mechanical & Plumbing', target: 'Fans', value: this.elecConsumptionRate/12},
              {source: 'Mechanical & Plumbing', target: 'Pumps', value: this.elecConsumptionRate/12},
              {source: 'Mechanical & Plumbing', target: 'Water Treatment', value: this.elecConsumptionRate/12},
              {source: 'Plug', target: 'Maker Space', value: this.elecConsumptionRate/32},
              {source: 'Plug', target: 'Coffee Cart', value: this.elecConsumptionRate/32},
              {source: 'Plug', target: 'Computer Services', value: this.elecConsumptionRate/32},
              {source: 'Plug', target: 'Miscellaneous', value: this.elecConsumptionRate/32},
            ],
        },
        ],
      };
      this.powerSankey.setOption(powerSankeyOption,true);
    },
    onResize() {
      if(this.powerSankey) _.debounce(this.powerSankey.resize, 100)()
    },
  },
  mounted: function () {
    this.powerSankey = echarts.init(this.$refs.powerSankey);
    this.refreshPowerSankey();
    this.onResize();
  }
}
</script>
